<template>
  <div class="multi-selector-input">
    <div class="multi-selector-input__input-wrapper" @click="showOptions">
      <div class="multi-selector-input__close-button" ref="closeButton">
        &#735;
      </div>
      <input
        class="multi-selector-input__input"
        :name="name"
        type="text"
        disabled
        :placeholder="placeholder"
        @input="valueInput"
        ref="input"
      />
    </div>
    <div class="multi-selector-input__options" ref="options">
      <div v-for="option in options" :key="option" @click="valueInput">
        <p class="multi-selector-input__option" ref="checkbox">
          {{ option }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SelectorInput',
  props: {
    name: {
      type: String,
      default: '',
      required: true,
    },
    options: {
      type: Array,
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      value: [],
    };
  },
  watch: {
    value() {
      this.$emit('updateField', { key: this.name, value: this.value });
    },
  },
  methods: {
    valueInput(event) {
      if (!this.value.includes(event.target.innerText)) {
        event.target.classList.add('multi-selector-input__option_active');
        this.value.push(event.target.innerText);
      } else {
        event.target.classList.remove('multi-selector-input__option_active');
        this.value.splice(this.value.indexOf(event.target.innerText), 1);
      }
      this.$refs.input.value = this.value;
    },
    showOptions() {
      if (this.$refs.options.style.display === 'block') {
        this.$refs.closeButton.style.display = 'none';
        this.$refs.options.style.display = 'none';
      } else {
        this.$refs.closeButton.style.display = 'block';
        this.$refs.options.style.display = 'block';
      }
    },
  },
};
</script>

<style scoped lang="sass">
.multi-selector-input
  position: relative

.multi-selector-input__input-wrapper

.multi-selector-input__input
  width: 100%
  border: 1px solid rgba(31, 32, 65, 0.5)
  box-sizing: border-box
  border-radius: 4px
  padding: 13px 15px
  font-size: 14px
  color: rgba(31, 32, 65, 0.75)
  cursor: pointer
  &::-webkit-input-placeholder
    color: rgba(31, 32, 65, 0.75)
  &:hover::-webkit-input-placeholder
    color: rgba(31, 32, 65, 1)
  &:hover
    border: 1px solid rgba(31, 32, 65, 0.75)
  &:focus
    border: 1px solid rgba(31, 32, 65, 0.75)
    outline: none

.multi-selector-input__options
  display: none
  width: 100%
  position: absolute
  background: white
  border: 1px solid rgba(31, 32, 65, 0.5)
  box-sizing: border-box
  border-radius: 4px
  font-size: 16px
  color: rgba(31, 32, 65, 0.75)
  cursor: pointer

.multi-selector-input__option
  margin: 0
  padding: 5px 5px 5px 10px
  &_active
    color: #BC9CFF


.multi-selector-input__close-button
  display: none
  position: absolute
  top: 10px
  right: 15px
  font-size: 35px
  cursor: pointer
</style>
