<template>
  <div class="checkbox-input">
    <label class="checkbox-input__element">
      <input class="checkbox-input__checkbox" type="checkbox" :name="name" :value="value" @input="valueInput" />
      <span class="checkbox-input__button"></span>
      <p class="checkbox-input__text">{{ text }}</p>
    </label>
  </div>
</template>

<script>
export default {
  name: 'CheckboxInput',
  props: {
    name: {
      type: String,
      default: '',
      required: true,
    },
    isChecked: {
      type: Boolean,
      default: false,
    },
    text: {
      type: String,
      default: '',
      required: true,
    },
  },
  data() {
    return {
      value: '',
    };
  },
  watch: {
    value() {
      this.$emit('updateField', { key: this.name, value: this.value });
    },
  },
  methods: {
    valueInput(event) {
      this.value = event.target.checked;
    },
  },
};
</script>

<style scoped lang="sass">
.checkbox-input

.checkbox-input__element
  cursor: pointer
  display: flex

.checkbox-input__checkbox
  display: none
  &:checked
    &+.checkbox-input__button::after
      display: block
    &+.checkbox-input__button
      border: 1px solid #BC9CFF

.checkbox-input__button
  display: inline-block
  margin-right: 10px
  min-width: 20px
  height: 20px
  border: 1px solid rgba(31, 32, 65, 0.5)
  box-sizing: border-box
  border-radius: 4px
  position: relative
  vertical-align: middle
  &::after
    display: none
    height: 12px
    width: 12px
    top: 3px
    left: 3px
    border-radius: 2px
    background: #BC9CFF
    content: ' '
    position: absolute

.checkbox-input__text
  margin: 0
  color: rgba(31, 32, 65, 0.85)
  transition-duration: 300ms
  &:hover
    color: rgba(31, 32, 65, 1)

</style>
