<template>
  <div class="radio-input">
    <label
      v-for="option in options"
      :key="option.value"
      class="radio-input__element"
    >
      <input
        class="radio-input__checkbox"
        type="radio"
        :name="name"
        :value="option.value"
        @input="valueInput"
      />
      <span class="radio-input__button"></span>
      <p class="radio-input__text">{{ option.text }}</p>
    </label>
  </div>
</template>

<script>
export default {
  name: 'RadioInput',
  props: {
    name: {
      type: String,
      default: '',
      required: true,
    },
    options: {
      value: { type: String, default: '', required: true },
      text: { type: String, default: '', required: true },
    },
  },
  data() {
    return {
      value: '',
    };
  },
  watch: {
    value() {
      this.$emit('updateField', { key: this.name, value: this.value });
    },
  },
  methods: {
    valueInput(event) {
      this.value = event.target.value;
    },
  },
};
</script>

<style scoped lang="sass">
.radio-input
  display: flex

.radio-input__element
  cursor: pointer
  margin-right: 15px

.radio-input__checkbox
  display: none
  &:checked
    &+.radio-input__button::after
      background: #BC9CFF
    &+.radio-input__button
      border: 1px solid #BC9CFF
    &+.radio-input__button+.radio-button__text
      color: rgba(31, 32, 65, 0.75)

.radio-input__button
  display: inline-block
  margin-right: 10px
  width: 20px
  height: 20px
  border: 1px solid rgba(31, 32, 65, 0.5)
  box-sizing: border-box
  border-radius: 10px
  position: relative
  vertical-align: middle
  &::after
    content: ""
    height: 12px
    width: 12px
    border-radius: 10px
    top: 3px
    left: 3px
    transition-duration: 100ms
    position: absolute
    z-index: 1


.radio-input__text
  margin: 0
  display: inline-block
  color: rgba(31, 32, 65, 0.85)
  transition-duration: 300ms
  &:hover
    color: rgba(31, 32, 65, 1)

</style>
