<template>
  <input
    class="text-field"
    :name="name"
    :type="type"
    :placeholder="placeholder"
    :value="value"
    @input="valueInput"
    v-model.trim="value"
  />
</template>

<script>
export default {
  name: 'TextField',
  props: {
    name: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: 'text',
    },
    placeholder: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      value: '',
    };
  },
  watch: {
    value() {
      this.$emit('updateField', { key: this.name, value: this.value });
    },
  },
  methods: {
    valueInput(event) {
      this.value = event.target.value;
    },
  },
};
</script>

<style scoped lang="sass">
.text-field
  border: 1px solid rgba(31, 32, 65, 0.5)
  box-sizing: border-box
  border-radius: 4px
  padding: 13px 15px
  font-size: 14px
  color: rgba(31, 32, 65, 0.75)

  &::-webkit-input-placeholder
    color: rgba(31, 32, 65, 0.75)

  &:hover::-webkit-input-placeholder
    color: rgba(31, 32, 65, 1)

  &:hover
    border: 1px solid rgba(31, 32, 65, 0.75)

  &:focus
    border: 1px solid rgba(31, 32, 65, 0.75)
    outline: none

input[type="Date"]
  cursor: text
  &:before
    content: attr(placeholder)
</style>
