<template>
  <div class="selector-input">
    <select class="selector-input__select" :name="name" @input="valueInput">
      <option
        class="selector-input__option selector-input__option_disabled"
        >{{ placeholder }}</option
      >
      <option
        class="selector-input__option"
        v-for="option in options"
        :key="option"
        >{{ option }}</option
      >
    </select>
  </div>
</template>

<script>
export default {
  name: 'SelectorInput',
  props: {
    name: {
      type: String,
      default: '',
      required: true,
    },
    options: {
      type: Array,
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      value: [],
    };
  },
  watch: {
    value() {
      this.$emit('updateField', { key: this.name, value: this.value });
    },
  },
  methods: {
    valueInput(event) {
      this.value = event.target.value;
    },
  },
};
</script>

<style scoped lang="sass">
.selector-input

.selector-input__select
  width: 100%
  border: 1px solid rgba(31, 32, 65, 0.5)
  box-sizing: border-box
  border-radius: 4px
  padding: 13px 15px
  font-size: 14px
  color: rgba(31, 32, 65, 0.75)
  cursor: pointer
  &:hover
    border: 1px solid rgba(31, 32, 65, 0.75)
  &:focus
    border: 1px solid rgba(31, 32, 65, 0.75)
    outline: none

.selector-input__option
  &_disabled
    display: none
</style>
