<template>
  <form
    @submit.prevent="onSubmit"
    @reset.prevent="onReset"
    class="form"
    ref="form"
  >
    <div class="form__section">
      <h3 class="form__title">Создание клиента</h3>
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="secondName"
        type="text"
        placeholder="Фамилия *"
        :value="secondName"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="firstName"
        type="text"
        placeholder="Имя *"
        :value="firstName"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="lastName"
        type="text"
        placeholder="Отчество"
        :value="lastName"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="dateBirth"
        type="date"
        placeholder="Дата рождения *:"
        :value="dateBirth"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <RadioInput name="gender" :options="genders" @updateField="updateField" />
    </div>
    <div class="form__section">
      <SelectorInput
        class="form__element"
        name="documentName"
        placeholder="Документ, удостоверяющий личность *"
        :options="documents"
        @updateField="updateField"
      />
    </div>
    <div class="form__section  form__section_multiple" v-if="isShowSection">
      <TextField
        class="form__element"
        name="documentSeries"
        type="text"
        placeholder="Серия"
        :value="documentSeries"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="documentNumber"
        type="text"
        placeholder="Номер"
        :value="documentNumber"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="documentIssued"
        type="text"
        placeholder="Кем выдан"
        :value="documentIssued"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="documentDate"
        type="date"
        placeholder="Дата выдачи *:"
        :value="documentDate"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="phoneNumber"
        type="text"
        placeholder="Номер телефона *"
        :value="phoneNumber"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <CheckboxInput
        class="form__element"
        name="noSentSMS"
        :text="sendSMS.text"
        :isChecked="sendSMS.isChecked"
        @updateField="updateField"
      />
    </div>
    <div class="form__section form__section_multiple">
      <TextField
        class="form__element"
        name="index"
        type="text"
        placeholder="Индекс"
        :value="index"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="country"
        type="text"
        placeholder="Страна"
        :value="country"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="region"
        type="text"
        placeholder="Область"
        :value="region"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="city"
        type="text"
        placeholder="Город *"
        :value="city"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="street"
        type="text"
        placeholder="Улица"
        :value="street"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="house"
        type="text"
        placeholder="Дом"
        :value="house"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <p class="form__banner">Группа пациента</p>
      <MultiSelectorInput
        class="form__element"
        name="customerGroup"
        placeholder="Выберите свою(-и) группу(-ы) *"
        :options="customerGroups"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <p class="form__banner">Лечащий врач</p>
      <SelectorInput
        class="form__element"
        name="doctor"
        placeholder="Выберите лечащего врача"
        :options="doctors"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <p class="form__help">*Поле обязательное для заполнения.</p>
    </div>
    <div class="form__section form__section_multiple">
      <button class="form__button form__button_reset" type="reset">
        Очистить
      </button>
      <button class="form__button form__button_submit" type="submit">
        Отправить
      </button>
    </div>
    <!-- <p>
      {{ firstName }} {{ secondName }} {{ lastName }} {{ dateBirth }}
      {{ phoneNumber }} {{ gender }} {{ noSentSMS }} {{ customerGroup }}
      {{ doctor }} {{ documentName }} {{ documentSeries }} {{ documentNumber }}
      {{ documentIssued }} {{ documentDate }}
    </p> -->
  </form>
</template>

<script>
import TextField from './TextField.vue';
import RadioInput from './RadioInput.vue';
import SelectorInput from './SelectorInput.vue';
import MultiSelectorInput from './MultiSelectorInput.vue';
import CheckboxInput from './CheckboxInput.vue';

export default {
  name: 'Form',
  components: {
    TextField,
    RadioInput,
    SelectorInput,
    MultiSelectorInput,
    CheckboxInput,
  },
  data() {
    return {
      // Отправляемые данные
      firstName: '',
      secondName: '',
      lastName: '',
      dateBirth: '',
      phoneNumber: '',
      gender: '',
      noSentSMS: false,
      customerGroup: [],
      doctor: '',
      index: '',
      country: '',
      region: '',
      city: '',
      street: '',
      house: '',
      documentName: '',
      documentSeries: '',
      documentNumber: '',
      documentIssued: '',
      documentDate: '',
      // Данные для заполнения
      genders: [
        { value: 'Male', text: 'Мужчина' },
        { value: 'Female', text: 'Женщина' },
      ],
      customerGroups: ['VIP', 'Проблемные', 'ОМС'],
      doctors: ['Иванов', 'Захаров', 'Чернышева'],
      documents: ['Паспорт', 'Свидетельство о рождении', 'Вод. удостоверение'],
      sendSMS: { isChecked: false, text: 'Не отправлять СМС' },
    };
  },
  computed: {
    isShowSection() {
      return this.documentName !== '';
    },
  },
  methods: {
    updateField(data) {
      this[data.key] = data.value;
    },
    onReset() {
      this.firstName = '';
      this.secondName = '';
      this.lastName = '';
      this.dateBirth = '';
      this.phoneNumber = '';
      this.gender = '';
      this.noSentSMS = '';
      this.customerGroup = '';
      this.doctor = '';
      this.index = '';
      this.country = '';
      this.region = '';
      this.city = '';
      this.street = '';
      this.house = '';
      this.documentName = '';
      this.documentSeries = '';
      this.documentNumber = '';
      this.documentIssued = '';
      this.documentDate = '';
      for (
        var i = this.$refs.form.querySelectorAll('input').length - 1;
        i >= 0;
        i--
      ) {
        this.$refs.form.querySelectorAll('input')[i].value = '';
      }
    },
    onSubmit() {
      console.log(this.firstName);
      console.log(this.secondName);
      console.log(this.lastName);
      console.log(this.dateBirth);
      console.log(this.phoneNumber);
      console.log(this.gender);
      console.log(this.noSentSMS);
      console.log(this.customerGroup);
      console.log(this.doctor);
      console.log(this.index);
      console.log(this.country);
      console.log(this.region);
      console.log(this.city);
      console.log(this.street);
      console.log(this.house);
      console.log(this.documentName);
      console.log(this.documentSeries);
      console.log(this.documentNumber);
      console.log(this.documentIssued);
      console.log(this.documentDate);
      // Sending data to the server
      // * magic *
      this.$refs.form.reset();
    },
  },
};
</script>

<style scoped lang="sass">
.form
  min-width: 300px
  display: flex
  flex-direction: column
  font-size: 18px
  padding: 15px
  background: white
  border: 1px solid rgba(0, 0, 0, 0.12)
  box-sizing: border-box
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2)
  border-radius: 4px
  font-family: Helvetica

.form__title
  margin: 0 auto
  padding: 15px 0
  color: rgba(31, 32, 65, 1)

.form__help
  margin: 0 auto
  padding: 15px 0
  color: rgba(31, 32, 65, 0.75)
  font-style: italic
  font-size: 0.8rem

.form__section
  display: flex
  flex-direction: column
  margin-bottom: 10px
  &_multiple
    margin-bottom: 0px
    flex-direction: row
    flex-wrap: wrap
    justify-content: space-between
    & .form__element
      margin-bottom: 10px
    & .form__element:nth-child(1),
    & .form__element:nth-child(3),
    & .form__element:nth-child(5)
      margin-right: 5px
    & .form__element:nth-child(2),
    & .form__element:nth-child(4),
    & .form__element:nth-child(6)
      margin-left: 5px
  @media screen and (max-device-width: 500px)
    &_multiple
      flex-direction: column
      & .form__element:nth-child(1),
      & .form__element:nth-child(3),
      & .form__element:nth-child(5),
      & .form__element:nth-child(2),
      & .form__element:nth-child(4),
      & .form__element:nth-child(6)
        margin-left: 0
        margin-right: 0

.form__banner
  margin: 0
  line-height: 30px
  color: rgba(31, 32, 65, 1)

.form__element
  flex-grow: 1

.form__button
  width: 200px
  height: 40px
  border: 1px solid rgba(31, 32, 65, 0.75)
  border-radius: 2px
  box-sizing: border-box
  transition-duration: 200ms
  cursor: pointer
  &:hover
    border: 1px solid rgba(31, 32, 65, 1)
  &:focus
    border: 1px solid rgba(31, 32, 65, 1)
    outline: none
  // background: white
  &_submit
    background: #98ff78
    &:hover
      background: #37a814
  &_reset
    &:hover
      background: #c2c2c2
  @media screen and (max-device-width: 500px)
    width: 100%
    &_submit
      margin-top: 10px
</style>
